

import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
  directives: {
    TransferDom
  },
  components: {
    Toast,
    Alert,
    Group,
    Cell,
    Badge,
    Tabbar,
    TabbarItem
  },
  data () {
    return {
      msg: 'Hello World!',
      show: false,
      isLoading: false
    }
  },
  methods: {
    showAlert () {
      this.$vux.alert.show({
        title: 'VUX is Cool',
        content: 'Do you agree?',
        onShow () {
          console.log('Alert: I\'m showing')
        },
        onHide () {
          console.log('Alert: I\'m hiding now')
        }
      })
    },
    showToast () {
      this.$vux.toast.show({
        text: '暂无权限',
        type: 'warn',
        onShow () {
          console.log('Toast: I\'m loading')
        },
        onHide () {
          console.log('Toast: I\'m hiding')
        }
      })
    },
    onHide () {
      console.log('on hide')
    },
    onShow () {
      console.log('on show')
    }
  }
}




