

import Loading from 'vux/src/components/loading/index.vue'


export default {
  data () {
    const id = this.$route.params.id || ''
    return {
      msg: '基金详情',
      id: id
    }
  },
  components: {
    Loading
  },
  mounted () {
    this.$vux.loading.show({
      text: '正在加载...'
    })
    setTimeout(() => {
      this.$vux.loading.hide()
    }, 300)
  }
}



